<template>
    <el-row :gutter="20">
        <el-col :span="8" :offset="12">
            <div v-show="!ifTableEdit" class="tableEditWrapper">
                <el-button class="edit" icon="edit" @click.native="tableEdit()">编辑</el-button>
            </div>
            <div v-show="ifTableEdit" class="tableEditWrapper">
                <el-button class="cancle" icon="edit" @click.native="tableCancle()">退出编辑</el-button>
            </div>
        </el-col>
        <el-col :span="19" class="leftReset">
            <el-form ref="form" label-position="right"  label-width="120px">
                <el-row type="flex" align="middle">
                    <el-col :span="14" :offset="2" v-show="ifTableEdit">
                        <el-button-group style='float:right'>
                            <el-button class="filter-item" @click="add()" type="primary" icon="edit">新建角色</el-button>
                            <!-- <el-button class="filter-item" @click="handleCreate" type="primary" icon="edit">行内新建</el-button> -->
                        </el-button-group>
                    </el-col>
                </el-row>
                <table class="table">
                    <thead class="thead">
                    <tr>
                        <th v-show="ifTableEdit">
                            <!-- <input type="checkbox" v-model="checkAll" > -->
                            <el-checkbox v-model="checkAll" @change="changeState()"></el-checkbox>
                        </th>
                        <th>角色名</th>
                        <th>角色描述</th>
                        <th>权限列表</th>
                        <th v-show="ifTableEdit">操作</th>
                    </tr>
                    </thead>

                    <tbody class="tbody">
                    <tr v-for="(item, $index) in roleDataList">
                        <td v-show="ifTableEdit">
                            <!-- <input type="checkbox"> -->
                            <el-checkbox v-model="item.checked" @change="singleSelect()"></el-checkbox>
                        </td>
                        <td>{{item.name}}</td>
                        <td>{{item.description}}</td>
                        <td >
                            <span v-for="ro in item.permissions">{{ro.name}} </span>
                        </td>
                        <td class="modifyTrData"  v-show="ifTableEdit" @click='edit(item)'><el-button>修改</el-button></td>
                    </tr>
                    </tbody>
                </table>

                <el-row type="fix" align="middle" style="margin-top: 20px">
                    <el-col :span="8">
                        <el-button @click='deleteRole' v-show="ifTableEdit">删除</el-button>
                    </el-col>
                    <el-col :span="16" style="float:right;">
                        <el-pagination
                                @size-change="sizeChange"
                                @current-change="handleCurrentChange"
                                :current-page="currentPage"
                                :page-sizes="pageSizes"
                                :page-size="pageSize"
                                layout="total, sizes, prev, pager, next, jumper"
                                :total="totalCount">
                        </el-pagination>
                    </el-col>
                </el-row>

            </el-form>
        </el-col>

        <el-dialog title="添加角色" :visible.sync="dialogFormVisible" :before-close="cancle">
            <el-form :model="role">
                <el-form-item label="角色名" :label-width="formLabelWidth" v-if="role.id == 1" prop="name">
                    <el-input v-model="role.name" placeholder="角色名" auto-complete="off" :disabled="true" ></el-input>
                </el-form-item>
                <el-form-item label="角色名" :label-width="formLabelWidth" v-else>
                    <el-input v-model="role.name" prop="name" auto-complete="off" placeholder="角色名" :disabled="false" ></el-input>
                </el-form-item>

                <el-form-item label="角色描述" :label-width="formLabelWidth" >
                    <el-input v-model="role.description" placeholder="角色描述"  :label-width="formLabelWidth" :disabled="false" ></el-input>
                </el-form-item>

                <el-form-item label="权限列表" :label-width="formLabelWidth">
                    <el-checkbox-group v-model="checkedroles">
                        <el-checkbox v-for="permission in permissions" :label="permission.id" :disabled="role.id == 1 && permission.id==1?true:false" :key="permission">{{permission.name}}</el-checkbox>
                    </el-checkbox-group>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="cancle()">取 消</el-button>
                <el-button type="primary" @click="submit()">确 定</el-button>
            </div>
        </el-dialog>

    </el-row>
</template>
<script>
    import role from '@/api/roles';
    import permission from '@/api/permissions';
    //    import ElButton from "../../../../../node_modules/element-ui/packages/button/src/button";
    export default {
//        components: {ElButton},
        props: {
            type: {  //弹出框出现状态：新建：0，编辑：1
                type: Number,
                require: true,
            }
        },
        beforeCreate() {
            this.temp = {};
            this.tableDataTemp ={};
        },
        mounted() {
            this.getRoleList();
        },
        data() {
            var validatePass = (rule, value, callback) => {

            };
            var validatePass2 = (rule, value, callback) => {

            };
            return {
                totalCount:0,
                currentPage:1,
                pageSize:10,
                pageSizes:[10,20,30,40],
                checkAll: false,
                dialogFormVisible: false,
                visible2: false,
                ifEdit: false,
                ifTableEdit: false,
                dialogType:1,
                seachSel: '',
                seachSelect: '',
                roleDataList: [],
                checkedroles:[],
                roles:[],
                permissions:[],
                ids:[],
                role: {
                    id:null,
                    name: null,
                    description: null,
                    permissions: null,
                },
                checked: [],
                // 弹出框数据
                dialogFormData: {},
                listIndex: '',
                listQuery: {
                    page: 1,
                    limit: 10
                },
                currentPage: 1,
                defaultProps: {
                    children: 'children',
                    label: 'label'
                },
                formLabelWidth: '120px',
            };
        },
        methods: {

            add(){
                this.dialogFormVisible=true;
                this.getAppPermissions();
            },
            edit(role) {
                this.dialogFormVisible=true;
                this.getAppPermissions();
                if (role != null) {
                    this.role = role;
                    for (var i = 0; i < role.permissions.length; i++) {
                        this.checkedroles.push(role.permissions[i].id);
                    }
                }
            },
            deleteRole(){
                let that = this;
                var selectData = this.roleDataList.filter(item => {
                    return item.checked
                });
                for(var i=0;i<selectData.length;i++) {
                    if(selectData[i].id==1){
                        that.getRoleList();
                        that.$message({type: 'error', message: '管理员角色不能删除'});
                    }else{
                        role.deleteRole(selectData[i].id).then(function (response) {
                            that.getRoleList();
                            if (response.code == 0) {
                                that.$message({type: 'success', message: '角色删除成功'});
                                that.getRoleList();
                            } else {
                                that.$message({type: 'error', message: '角色删除失败'});
                            }
                        });
                    }

                }
            },
            getAppPermissions(){
                let that = this;
                permission.getPermissionsList({
                    page: 1,
                    length: 1000,
                }).then(function (response) {
                    if (response.code == 0) {
                        that.permissions = response.data.rows;
                    }
                });
            },
            handleCurrentChange (val) {
                this.currentPage = val;
                this.getRoleList();
            },
            sizeChange(val){
                this.pageSize = val;
                this.getRoleList();
            },
            getRoleList(){
                setTimeout(() => {
                    let that = this;
                    role.getRoleList({
                        page: that.currentPage,
                        length: that.pageSize
                    }).then(function (response) {
                        if (response.code == 0) {
                            that.roleDataList = response.data.rows;
                            that.totalPage = response.totalPage;
                            that.totalCount = response.data.totalCount;
                        }
                        else that.$message({type: 'error', message: response.reason});
                    });
                }, 300);
            },
            submit() {
                let that = this;
                that.role.permissions = that.checkedroles;
                if(that.role.id==null) {
                    if ("" != that.role.name && null != that.role.name) {
                                    role.create(that.role).then(function (response) {
                                        if (response.code == 0) {
                                            that.$message({type: 'success', message: "创建角色成功"});
                                            that.cancle();
                                            that.getRoleList();
                                        }
                                        else that.$message({type: 'error', message: "创建角色失败"});
                                        that.cancle();
                                        that.getRoleList();
                                    });

                    } else {
                        that.$message({type: 'error', message: "创建角色失败!请输入角色名"});
                    }
                }else{
                    role.update(that.role).then(function (response) {
                        if (response.code == 0) {
                            that.$message({type: 'success', message: "编辑角色成功"});
                            that.cancle();
                            that.getRoleList();
                        }
                        else that.$message({type: 'error', message: response.reason});
                    });
                }
            },
            cancle() {
                this.ifEdit = false;
                this.dialogFormVisible = false;
                this.role={
                    name: null,
                    description: null,
                    permissions: null,
                },
                this.checkedroles = [];
                this.getRoleList();
            },
            confirm() {
                this.ifEdit = false;
            },
            tableEdit() {
                this.ifTableEdit = true;
            },
            tableCancle() {
                this.ifTableEdit = false;
            },
            handleRemove(file, fileList) {
            },
            // 行内创建表格
//            handleCreate(data) {
//                this.userDataList.splice(0, 0, data);
//            },
            // 全选、单选
            changeState(){
                let that = this;
                let checked = this.checkAll;
                this.roleDataList.forEach(item => {
                    this.$set(item,"checked",checked)
                    item.checked = checked;
                    if(item.checked =checked){
                        that.ids.push(item.id);
                    }
                    console.log(item.checked+"*******"+item.id);
                })
            },
            singleSelect() {
                //console.log(this.ids.length);
//                var selectData = this.userDataList.filter(item => {
//                if(item.checked==true){
//                    console.log(item.id);
//                }
//                    if(item.checked == true){
//                        this.ids.push(item.id);
//                    }else{
//                        for(var i=0;i<this.ids.length;i++){
//                            if(this.ids[i]==item.id){
//                                this.ids.splice(this.ids[i]);
//                            }
//                        }
//                   }
//                    return item.checked == true
//                })
//                selectData.length == this.userDataList.length ? this.checkAll = true : this.checkAll = false;
            },


        },
        // 弹框取消按钮
        editCancle() {
            this.dialogFormData = this.tableDataTemp;
            console.log(this.dialogFormData,"this.dialogFormData");
            this.dialogFormVisible=false;
        },
        // 弹框确定按钮
        editDo(){
            // 请求后台
            this.dialogFormVisible=false;
        },
        // 分页
//            handleSizeChange(val) {
//                console.log(`每页 ${val} 条`);
//            },
//            handleCurrentChange(val) {
//                this.currentPage = val;
//                console.log(`当前页: ${val}`);
//            },

        filters: {
            formatDate(time) {
                var date = new Date(time);
                return parseTime(date, '{y}-{m}-{d}');
            }
        }
    };
</script>
<style lang="scss">
    @import "~styles/variable";

    .seachpre {
        width: 74px;
    }
    // button
       .editWrapper {
           float: right;
    .edit {
        width: 76px;
        padding: 7px 15px;
        color: $color-theme;
        border: 1px solid $color-theme;
    }
    }
    .filter-item {
        width: 100px;
    // width: 130px;
        height: 36px;
        margin-left: 20px;
    // color: $color-theme;
        border: 1px solid $color-theme;
    // background: none;
        border-radius: 4px !important;
    }

    .buttonWrapper {
        font-size: 12px;
        float: right;
    .confirm,
    .cancle {
        width: 76px;
        padding: 7px 15px;
    }
    .confirm {
        color: $color-theme;
        border: 1px solid $color-theme;
    }
    .cancle {
        color: $color-red;
        border: 1px solid $color-red;
    }
    }
    .tableEditWrapper {
        float: right;
    .edit {
        width: 102px;
        padding: 8px 15px;
        font-size: 13px;
        color: $color-theme;
        border: 1px solid $color-theme;
    }
    .cancle {
        width: 102px;
        padding: 8px 15px;
        font-size: 13px;
        color: $color-red;
        border: 1px solid $color-red;
    }
    }
    //  弹框
        .el-dialog__body .el-input,
        .el-dialog__body .el-date-editor {
            width: 360px !important;
        }
    // 分页
       .el-pagination {
           text-align: right;
           padding: 10px 0;
           float: right;
       }
    .el-pagination__total {
        float: left;
    }
    .upload-demo {
        float: left;
    .el-button {
        color: $color-theme;
        border-radius: 0px;
        background: none;
        border: 1px solid $color-theme;
    }
    }
    .loadTemplate {
        color: #2f3445;
        background: none;
        border: solid 1px #c0ccda !important;
    }
    .leftReset {
        padding-right: 20px !important;
    }
    .rightTitle {
        height: 50px;
        padding: 18px 0 18px 10px;
        line-height: 50px;
        box-sizing: border-box;
        background-color: #eff2f7;
    .form-group-title {
        margin: 0;
    }
    }
    // 表格
       .table {
           color: #2f3445;
           font-size: 13px;
           border: 1px solid #e0e6ed;
    .thead {
        background-color: #e5e9f2;
    th {
        width: 130px;
        height: 44px;
        text-align: center;
        border-right: 1px solid #e0e6ed;
    }
    }
    .tbody {
    tr {
        height: 46px;
        width: 130px;
        text-align: center;
        border-top: 1px solid #e0e6ed;
    td {
        border-right: 1px solid #e0e6ed;
    }
    .modifyTrData {
        color: #5c7dfe;
    }
    }
    }
    }
</style>



